"use client"

import { useState, useEffect } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { FileUp } from "lucide-react"

export default function SoumettrePage() {
  const searchParams = useSearchParams()
  const idEtudiant = searchParams.get("idEtudiant") || "4" // Default for testing

  // State for form files
  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [presentationFile, setPresentationFile] = useState<File | null>(null)

  // State for submitted documents
  const [submittedDocuments, setSubmittedDocuments] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)

  // Map livrable IDs to human-readable names
  const livrableNames = {
    1: "Document PDF",
    2: "Présentation",
  }

  // Fetch submitted documents when component mounts
  useEffect(() => {
    const fetchDocuments = async () => {
      setIsLoading(true)
      setError(null)
      try {
        const response = await fetch(
          `http://localhost:5000/api/livrable/documents?idEtudiant=${idEtudiant}`
        )
        if (response.ok) {
          const data = await response.json()
          setSubmittedDocuments(data)
        } else {
          setError("Failed to fetch documents")
        }
      } catch (error) {
        setError("Error fetching documents")
      } finally {
        setIsLoading(false)
      }
    }
    fetchDocuments()
  }, [idEtudiant])

  // Handle file selection
  const handlePdfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPdfFile(e.target.files[0])
  }

  const handlePresentationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPresentationFile(e.target.files[0])
  }

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    console.log("Soumission du formulaire...")

    if (!pdfFile || !presentationFile) {
      alert("Veuillez sélectionner les deux fichiers.")
      return
    }

    const formData = new FormData()
    formData.append("files", pdfFile)
    formData.append("files", presentationFile)
    formData.append("idEtudiant", idEtudiant)
    formData.append("idGroupe", "2") // Hardcoded for testing
    formData.append("livrables", JSON.stringify([1, 2])) // 1 for PDF, 2 for presentation

    try {
      const response = await fetch("http://localhost:5000/api/livrable/submit-documents", {
        method: "POST",
        body: formData,
      })

      if (response.ok) {
        alert("Soumission réussie!")
        // Optionally refresh the document list after submission
        const fetchResponse = await fetch(
          `http://localhost:5000/api/livrable/documents?idEtudiant=${idEtudiant}`
        )
        if (fetchResponse.ok) {
          const data = await fetchResponse.json()
          setSubmittedDocuments(data)
        }
      } else {
        const errorText = await response.text()
        alert("Erreur lors de la soumission: " + errorText)
      }
    } catch (error) {
      console.error("Erreur de connexion:", error)
      alert("Erreur de connexion au serveur")
    }
  }

  return (
    <Card className="w-full max-w-2xl mx-auto shadow-md">
      <CardHeader>
        <CardTitle>Soumettre des documents</CardTitle>
      </CardHeader>
      <CardContent>
        {/* Submitted Documents Section */}
        <div className="mb-6">
          <h2 className="text-lg font-semibold mb-2">Documents Submitted</h2>
          {isLoading ? (
            <p>Loading documents...</p>
          ) : error ? (
            <p className="text-red-500">{error}</p>
          ) : submittedDocuments.length > 0 ? (
            <ul className="list-disc pl-5 space-y-1">
              {submittedDocuments.map((doc) => (
                <li key={`${doc.idPFE}-${doc.idLivrable}`}>
                  {livrableNames[doc.idLivrable]}:{" "}
                  <a
                    href={`http://localhost:5000${doc.fichier}`}
                    download
                    className="text-blue-500 underline"
                  >
                    Download
                  </a>
                </li>
              ))}
            </ul>
          ) : (
            <p>No documents submitted yet.</p>
          )}
        </div>

        {/* Submission Form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="pdf" className="block text-sm font-medium text-gray-700">
              Document PDF
            </label>
            <input
              type="file"
              id="pdf"
              accept=".pdf"
              onChange={handlePdfChange}
              className="mt-1 block w-full"
            />
          </div>
          <div>
            <label
              htmlFor="presentation"
              className="block text-sm font-medium text-gray-700"
            >
              Présentation
            </label>
            <input
              type="file"
              id="presentation"
              accept=".ppt,.pptx,.key,.pdf"
              onChange={handlePresentationChange}
              className="mt-1 block w-full"
            />
          </div>
          <Button type="submit" className="w-full">
            <FileUp className="mr-2 h-4 w-4" />
            Soumettre
          </Button>
        </form>
      </CardContent>
    </Card>
  )
}